import React, { Component } from 'react'
import ReactDOM from 'react-dom'
import PropTypes from 'prop-types'
import Paper from 'material-ui/Paper';
//import {List, ListItem} from 'material-ui/List';
import Chip from 'material-ui/Chip';
import SvgIconFace from 'material-ui/svg-icons/action/face';
import Avatar from 'material-ui/Avatar';

export class RecordList extends Component {
    componentDidUpdate(){
        this.div && (this.div.scrollTop = this.div.scrollHeight);
    }

    render(){

        return (
    <Paper key={"paper"} style={{maxHeight: 200, overflow: 'auto', scrollTop:"200px"}}
    ref={(component)=>{ this.div=ReactDOM.findDOMNode(component); }}
    > 
            {this.props.items.map((item,index)=>{console.log(index, item);return(
                 <Chip key={item.timestamp} style={item.from?{margin:4, marginRight:8, marginLeft:8, float:"right", clear:"both"}:{margin:4, marginRight:8, marginLeft:8, float:"left", clear:"both"}}
                  backgroundColor={!item.from?"#FFA500":"#00EE00"}>
                    <p >{item.text} </p>
                 </Chip>

            )})}
    </Paper>

)
    }
}

RecordList.propTypes = {
    items: PropTypes.arrayOf(PropTypes.shape({
         text: PropTypes.string.isRequired,
         
    }).isRequired).isRequired,
}

export default RecordList



