export const userSend=(value)=>(dispatch, getState)=>{
    console.log("getState", getState())
    let ws = getState().ws
    console.log("ws!!!", ws, Object.keys(ws))
    ws.send(value)
    dispatch({ type: "INPUT_SEND", value: value })
}

export const userChange=(value)=>({
    type: "INPUT_CHANGED",
    value: value
})

