var W3CWebSocket = require('websocket').w3cwebsocket;
// let or var
let ws = new W3CWebSocket ('ws://localhost:8091/ws')
function WsBindDispatch(dispatch, client){
    client.onerror = function() {
        console.log('Connection Error');
        dispatch({type: "WS_ERROR", value:"ws error"})
    };
    client.onopen = function() {
        dispatch({type: "WS_OPEN", value:client})
    };
    client.onclose = function() {
        console.log('echo-protocol Client Closed');
        dispatch({type: "WS_CLOSE", value:"ws close"})
        // changed the 'ws' pointed?
        setTimeout(()=>{
        client = new W3CWebSocket('ws://localhost:8091/ws')
        console.log('client', client)
        WsBindDispatch(dispatch, client)})
    };

    client.onmessage = function(e) {
        if (typeof e.data === 'string') {
            console.log("Received: '" + e.data + "'");
        dispatch({type: "WS_MESSAGE", value:e.data, timestamp:new Date()})
        }
    };
}

export {
 WsBindDispatch,
    ws // 重连之后是否会指向新的对象？
}



