import React from 'react'
import { connect } from 'react-redux'
import CUserInput from '../components/input'
import {userSend, userChange} from '../actions'

const mapState = (state) => ({
    value: state.value,
})

const mapDispatch = {
    onClick: userSend,
    onChange: userChange,
}

const UserInput = connect(mapState, mapDispatch)(CUserInput)

export default UserInput



