import React from 'react'
import PropTypes from 'prop-types'
import RaisedButton from 'material-ui/RaisedButton';
import TextField from 'material-ui/TextField';

const UserInput = (({value, onClick, onChange})=>(
        <div>
            <div style={{float:"left", width:"80%"}}>
                <TextField
                        onKeyPress={(e)=>{console.log(e.key, e.shiftKey)
                            if(!e.shiftKey &&(e.key==='Enter')){
                                onClick(value.trim()) 
                            }
                        }}
                        id="text-field-controlled"
                        value={value}
                        onChange={(e, newValue)=>{console.log(e.target.value, newValue);onChange(e.target.value);}}
                        rows={5}
                        multiLine={true}
                        fullWidth={true}
                        textareaStyle={{marginLeft:"18px"}}
                    />
            </div>
            <div style={{float:"right", width:"20%",height:"120px"}}>
                <RaisedButton label="发送" style={{lineHeight: '134px'}} buttonStyle={{width:"100%", height:"127px", marginTop:"5px"}} fullWidth={true} onClick={()=>onClick(value)} disabled={value.trim()===""}/>
            </div >
        </div>
))

export default UserInput



