import { combineReducers } from 'redux'

const reduce_send =(state=[], action)=>{
    switch (action.type) {
        case "INPUT_SEND":
            return [...state, {
                text:action.value,
                timestamp: new Date(),
            }]
        case "WS_MESSAGE":
            return [...state, {
                text:action.value,
                from: "mars",
                timestamp: new Date(),
            }]
        default:
            return state
    
    }
}

const reduce_change =(state="", action)=>{
    switch (action.type) {
        case "INPUT_CHANGED":
            console.log(state, action)
            return action.value 
        case "INPUT_SEND":
            return "" //清空
        default:
            return state
    }
}

const reduce_websocket =(state=null, action)=>{
    switch (action.type) {
        case "WS_OPEN":
            console.log('rrreducer', state, action)
            return action.value 
        default:
            return state
    }
    return state
}

export const rootReducer = combineReducers({
    items: reduce_send,
    value: reduce_change, 
    ws: reduce_websocket,
}) 



